  background-color: var(--color-bg-context-menu);
  border-radius: 0.5rem;
  font-size: 0.875rem;
  padding: 0.4375rem 0.25rem;
  position: absolute;
  z-index: 1;
}

.options-menu-label,
.menu-option-button {
  padding-inline: 0.5rem;
}

.options-menu-label {
  color: var(--color-fg-faint);
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-wrap: nowrap;
}

.menu-option-button {
  background-color: transparent;
  border-radius: 0.5rem;
  color: var(--color-fg-primary);
  padding-block: 0.25rem;
  text-align: start;
  vertical-align: middle;
  width: 100%;
}

.menu-option-button:hover {
  background-color: var(--color-bg-context-menu-highlight);
}

.confirm-deletion-button {
  color: var(--color-fg-critical);
}
