___CSS_LOADER_EXPORT___.push([module.id, `.board-container:not(.hidden) {
  display: grid;
  height: 100%;
}

.board-container {
  max-height: 100vh;
  padding: 1.25rem;

  grid-template-columns: 1fr max-content;
  grid-template-rows: max-content 1fr;
  gap: 1rem 2rem;
}

.board-title {
  font-size: 1.25rem;
  font-weight: bold;
  overflow-x: hidden;
  text-overflow: ellipsis;
  text-wrap: nowrap;
}

.create-list-button {
  background-color: transparent;
  color: var(--color-fg-secondary);

  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.create-list-button:hover {
  color: var(--color-fg-highlight);
}

.lists-container {
  grid-column: span 2;
  overflow-x: auto;
  position: relative;

  display: flex;
  align-items: start;
  gap: 1rem;
}

.lists-container > * {
  flex-shrink: 0;
}
